import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-apply-constract',
  templateUrl: './apply-constract.component.html',
  styleUrls: ['./apply-constract.component.css']
})
export class ApplyConstractComponent implements OnInit {
	title = 'app';
  tabIndex = 1;
  tabChaged(index){
  	console.log(index);
  	// 让被绑定的tabIndex等于当前点击的下标
  	this.tabIndex = index;//此处的this表示当前组件
  }
	user={
		link:"s",
	};
    genders = [{value:"s",display:'QQ'},{value:"f",display:'邮箱'},{value:"m",display:'MSN'}]
    menuIdex=1;
	changeMenu(index){
		this.menuIdex = index;
	}
	// 作品介绍
	bookIntro='';
	// 最大字数限制
	maxSize = 10;
	// 当前字数
	nowSize=0;
	// 监听文本输入
	textChanged(){
		console.log("-=-=-=-"+this.bookIntro.length);
		this.nowSize = this.bookIntro.length;
	}
	dataList=[
		{name:"春光无限",time:"2017-08-18 10：01", type:"2017-08-18 10：01",money:"审核中",bei:"..."},
		
		
	]
  constructor() { }

  ngOnInit() {
  }

}



