import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-apply-grouding',
  templateUrl: './apply-grouding.component.html',
  styleUrls: ['./apply-grouding.component.css']
})
export class ApplyGroudingComponent implements OnInit {
  title = 'app';
  tabIndex = 1;
  tabChaged(index){
  	console.log(index);
  	// 让被绑定的tabIndex等于当前点击的下标
  	this.tabIndex = index;//此处的this表示当前组件
  }
  menuIdex=1;
	changeMenu(index){
		this.menuIdex = index;
	}
	// 作品介绍
	bookIntro='';
	// 最大字数限制
	maxSize = 10;
	// 当前字数
	nowSize=0;
  constructor() { }

  ngOnInit() {
  }

}



